
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'FileLogin',
components: {
XHeader
},
created(){
},
data () {
return {
username:'',
idcard:'',
disable: true,
}

},
methods: {
NextStep(){
if(!this.disable){
this.$root.loginInfo.CSFZH = this.idcard;
this.$root.loginInfo.CHZXM =this.username;
this.$router.push({name: "UploadIDCard", params: {username: this.username, idcard: this.idcard}})
}
}
},
watch: {
idcard(val){
if(val.length == 18){
this.disable = false;
return;
}
this.disable = true;
}
}
}



